// CORE (blog or generic)
.core {
    display: flex;
    flex-direction: row;
    padding-top: 0;
    padding-bottom: 0;

    .article {
        width: 70%;
        margin: 40px 0px;

        .the-article {
            width: 70%;
            margin: 0 auto;

            img {
                width: 100%;
                padding: 50px 0;
                border-radius: 40px;
                overflow: hidden;
            }
        }

        @include tablette {
            width: 100%;
        }
    }

    .widgets {
        width: 30%;
        h2, h3, h4, h5, .blog__block2 {
            max-width: 350px;
            margin: auto;
            margin-bottom: $pad2;
        }
        .articles {
            padding: 50px 0;
        }

        @include tablette {
            display: none;
        }
    }

    // BLOCKS
    .blog__block2 {
        border-radius: $pad2;
        aspect-ratio: 4/3;
        display: flex;
        align-items: center;
        background-color: $white;
        padding: $pad1;
        box-shadow: $ombre001;

        .blog__image {
            width: 850px;
            // height: 90%;

            img {
                width: 100%;
                border-radius: $pad1;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        .blog__text {
            margin: 0 $pad1;

            h6 {
                margin: 0;
            }
            p {
                height: 5.5em;
                overflow: hidden;
            }
            .button {
                margin-top: 5px;
            }
        }
    }

    .art-categories {
        display: flex;
        flex-direction: row;
        width: 70%;
        margin: 0 auto;

        .__single-cat {
            display: flex;
            flex-direction: row;
        }

        .__icon {
            width: 15px;
        }
    }

    // LEFT

    .blog__image {
        img {
            width: 100%;
        }
    }

    .__thetags {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .__icon {
        width: 15px;
    }

    // modifiers
    .--bigtitle {
        font-size: $title2-elastic;
    }

}